//parallax backgrounds

.layer {
  position: absolute;
  left: 0;
  width: 100%;
  height: 100vh;
  min-height: 480px;

  &.none-height {
    height: 0;
  }

  &.bottom {
    bottom: 0;
  }

  &.top {
    top: 0;
  }
}

.section-1 {
  .layer {
    .bg-layer-1 {
      width: 100%;
      height: 100%;
      min-height: 365px;
      max-height: 550px;
      margin-top: -220px;
      background-image: url(../img/mounts.png);
      background-size: contain;
      background-position: center bottom;
      background-repeat: no-repeat;
    }

    .bg-layer-2 {
      width: 153px;
      height: 500px;
      margin: 90px auto 0;
      background-image: url("../img/pack-2.png");
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }

    .bg-layer-3 {
      position: absolute;
      width: 100%;
      height: 1008.48px;
      bottom: -590px;
      background-image: url(../img/clouds.png);
      background-size: cover;
      background-position: center top;
      background-repeat: no-repeat;
    }

    .vitagem_text {
      font-family: 'Lato', sans-serif;
      font-style: normal;
      font-weight: 900;
      line-height: normal;
      font-size: 175px;
      width: 100%;
      position: absolute;
      text-align: center;
      padding-top: 55px;
      letter-spacing: 0.31em;
      text-transform: uppercase;
      background: -webkit-linear-gradient(#FFFFFF, #72ACCC);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      span{
        font-weight: 300;
      }
    }
  }
}
